      let needCoins = 0;

      while (needCoins * course < 1) {
        needCoins++;
      }

      document.querySelector(
        "#toPay"
      ).innerHTML = `–ú–∏–Ω–∏–º–∞–ª—å–Ω–æ–µ –∫–æ–ª-–≤–æ ${needCoins} DurkaCoins`;
