import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  Button,
  Group,
  Link,
} from "@vkontakte/vkui";

import {
  InventoryItem,
  MiningInventoryItem,
  ListUsers,
  ExchangeSell,
  ExchangeBuyItem,
  ModalExchangeBuy,
} from "../components";

import { formatCoins, SocketManager, formatCoinsToServer } from "../helpers";
const { callMethod } = SocketManager;

const Exchange = ({
  id,
  dataInventory,
  targetIdInventory,
  openUseItem,
  scheme,
  goHome,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  openBuyItem,
  updateConnection,
  socketReconnection,
  snackbar,
  dataMiners,
  miners,
  durkaCoins,
  course,
  openAlertPay,
  setActiveModal,
  openBuyDurkaCoins,
  rubles,
  exchangeQiwiPay,
  openReplenishment,
  openWithdraw,
  setActivePanel,
  fetchedUser,
  openSharingModal,
  openPaymentVIP,
  openInfoVIP,
  isVIP,
  vipType
}) => {
  let amountDurkaCoins = 0;
  const [costOneDC, setCostOneDC] = useState("");
  const [qiwiAccount, setQiwiAccount] = useState("+7");
  const [inputDurkaCoins, setInputDurkaCoins] = useState("");
  const [sliceItem, setSliceItem] = useState(50);

  // const [amountDurkaCoins, setAmountDurkaCoins] = useState("");

  const [activeTab, setActiveTab] = useState("buy");
  const [dataExchangeBuy, setDataExchangeBuy] = useState([]);
  let targetIdExchangeBuy = 0;

  function tabBuy() {
    setActiveTab("buy");
    window.scrollTo(0, 0);
  }

  function tabSell() {
    setActiveTab("sell");
    window.scrollTo(0, 0);
  }

  function getExchangeList() {
    callMethod("getExchangeList").then(({ message }) => {
      setDataExchangeBuy(message);
      // setDataExchangeBuy([]);
    });
  }

  function deleteExchangeOffer(userId) {
    console.log("value", userId);

    callMethod("deleteExchangeOffer", {
      ownerId: userId, // –ø–µ—Ä–µ–¥–∞—Ç—å –∞–π–¥–∏ —é–∑–µ—Ä–∞ —á—å–µ –ø—Ä–µ–¥–ª–æ–∂–µ–Ω–∏–µ —É–¥–∞–ª—è–µ–º
    }).then(({ status, message }) => {
      openAlertPay(message, status === "denied" ? "red" : "green");

      if (status === "success") {
        getExchangeList();
      }
    });
  }

  useEffect(() => {
    getExchangeList();
  }, []);

  function addExchangeOffer() {
    if (!inputDurkaCoins || !costOneDC || !qiwiAccount) {
      return openAlertPay("–¢—ã –∑–∞–±—ã–ª –∑–∞–ø–æ–ª–Ω–∏—Ç—å –≤—Å–µ –ø–æ–ª—è", "red");
    }

    console.log({
      inputDurkaCoins,
      costOneDC,
      qiwiAccount,
    });

    if (!qiwiAccount.startsWith("+7")) {
      // –ú–æ–¥–∞–ª–∫—É –æ —Ç–æ–º —á—Ç–æ –¥–æ–ª–∂–µ–Ω –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è –Ω–∞ +7
      return openAlertPay("–í–≤–µ–¥–∏ –Ω–æ—Ä–º–∞–ª—å–Ω—ã–π –Ω–æ–º–µ—Ä —à–∏–∑–æ–∏–¥", "red");
    }

    if (
      Number.isNaN(+inputDurkaCoins) ||
      (+inputDurkaCoins ^ 0) !== +inputDurkaCoins
    ) {
      return openAlertPay(
        "–ö–æ–ª-–≤–æ DurkaCoins –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å —Ü–µ–ª—ã–º —á–∏—Å–ª–æ–º, —Ç–µ–±—è –∫—Ç–æ –∏–∑ –ø–∞–ª–∞—Ç—ã –≤—ã–ø—É—Å—Ç–∏–ª?",
        "red"
      );
    }

    if (costOneDC.length > 4) {
      return openAlertPay("–í —á–∏—Å–ª–µ –¥–æ–ª–∂–Ω—ã –±—ã—Ç—å 2 –∑–Ω–∞–∫–∞ –ø–æ—Å–ª–µ –∑–∞–ø—è—Ç–æ–π", "red");
    }

    if (costOneDC >= course) {
      return openAlertPay(
        "–°—Ç–æ–∏–º–æ—Å—Ç—å –æ–¥–Ω–æ–≥–æ DurkaCoin –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –Ω–∏–∂–µ —Ç–µ–∫—É—â–µ–≥–æ –∫—É—Ä—Å–∞",
        "red"
      );
    }

    let oneLvl = +(course * 0.5).toFixed(2); // –∫–æ–ª-–≤–æ –∫–æ–∏–Ω–æ–≤ < 51
    let twoLvl = +(course * 0.4).toFixed(2); // –∫–æ–ª-–≤–æ –∫–æ–∏–Ω–æ–≤ > 51
    let threeLvl = +(course * 0.3).toFixed(2); // –∫–æ–ª-–≤–æ –∫–æ–∏–Ω–æ–≤ > 251
    let vipLvl = +(course * 0.2).toFixed(2); // –µ—Å–ª–∏ –≤–∏–ø
    let goldLvl = +(course * 0.1).toFixed(2); // –µ—Å–ª–∏ –≥–æ–ª–¥

    // ([236908027, 457421325, 597298271, 457203217, 432350015, 597232247].includes(fetchedUser.id))

    // console.log("isVIP", isVIP);
    // console.log("inputDurkaCoins", +inputDurkaCoins);
    // console.log("+costOneDC", +costOneDC);
    // console.log("oneLvl", oneLvl);
    // console.log("twoLvl", twoLvl);
    // console.log("threeLvl", threeLvl);
    // console.log("vipLvl", vipLvl);
    // console.log("vipType", vipType);

    // —É—Å–ª–æ–≤–∏—è –¥–ª—è –≤–∏–ø –∞–∫–∫–∞—É–Ω—Ç–æ–≤
    let _costOneDc = Number(costOneDC);
    let _inputDurkaCoins = Number(inputDurkaCoins);

    if (isVIP && vipType === 2 && _costOneDc < goldLvl) {
      return openAlertPay(
        `–î–ª—è –≤–∞—à–µ–≥–æ –∫–æ–ª-–≤–∞ DurkaCoins, —Å—Ç–æ–∏–º–æ—Å—Ç—å 1 DurkaCoins –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –≤—ã—à–µ ${goldLvl} —Ä—É–±`,
        "red"
      );
    } else if (isVIP && vipType === 1 && _costOneDc < vipLvl) {
      return openAlertPay(
        `–î–ª—è –≤–∞—à–µ–≥–æ –∫–æ–ª-–≤–∞ DurkaCoins, —Å—Ç–æ–∏–º–æ—Å—Ç—å 1 DurkaCoins –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –≤—ã—à–µ ${vipLvl} —Ä—É–±,
        –∫—É–ø–∏—Ç–µ VIP –ê–∫–∫–∞—É–Ω—Ç —á—Ç–æ–±—ã –ø—Ä–æ–¥–∞–≤–∞—Ç—å –¥–µ—à–µ–≤–ª–µ`,
        "red"
      );
    } else if (!isVIP && _inputDurkaCoins <= 50 && _costOneDc < oneLvl) {
      return openAlertPay(
        `–î–ª—è –≤–∞—à–µ–≥–æ –∫–æ–ª-–≤–∞ DurkaCoins, —Å—Ç–æ–∏–º–æ—Å—Ç—å 1 DurkaCoins –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –≤—ã—à–µ ${oneLvl} —Ä—É–±,
        –∫—É–ø–∏—Ç–µ VIP –ê–∫–∫–∞—É–Ω—Ç —á—Ç–æ–±—ã –ø—Ä–æ–¥–∞–≤–∞—Ç—å –¥–µ—à–µ–≤–ª–µ`,
        "red"
      );
    } else if (!isVIP && _inputDurkaCoins >= 51 && _inputDurkaCoins <= 250 && _costOneDc < twoLvl) {
      return openAlertPay(
        `–î–ª—è –≤–∞—à–µ–≥–æ –∫–æ–ª-–≤–∞ DurkaCoins, —Å—Ç–æ–∏–º–æ—Å—Ç—å 1 DurkaCoins –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –≤—ã—à–µ ${twoLvl} —Ä—É–±,
        –∫—É–ø–∏—Ç–µ VIP –ê–∫–∫–∞—É–Ω—Ç —á—Ç–æ–±—ã –ø—Ä–æ–¥–∞–≤–∞—Ç—å –¥–µ—à–µ–≤–ª–µ`,
        "red"
      );
    } else if (!isVIP && _inputDurkaCoins >= 251 && _costOneDc < threeLvl) {
      return openAlertPay(
        `–î–ª—è –≤–∞—à–µ–≥–æ –∫–æ–ª-–≤–∞ DurkaCoins, —Å—Ç–æ–∏–º–æ—Å—Ç—å 1 DurkaCoins –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –≤—ã—à–µ ${threeLvl} —Ä—É–±,
        –∫—É–ø–∏—Ç–µ VIP –ê–∫–∫–∞—É–Ω—Ç —á—Ç–æ–±—ã –ø—Ä–æ–¥–∞–≤–∞—Ç—å –¥–µ—à–µ–≤–ª–µ`,
        "red"
      );
    }

    if (Number.isNaN(+costOneDC)) {
      return openAlertPay("–°—Ç–æ–∏–º–æ—Å—Ç—å –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å —á–∏—Å–ª–æ–º, –∞–ª–ª–æ...", "red");
    }

    if (inputDurkaCoins * 100_000 > durkaCoins) {
      return openAlertPay("–£ —Ç–µ–±—è –Ω–µ —Ö–≤–∞—Ç–∞–µ—Ç –¥–µ–Ω—è–∫ –Ω–∞ –±–∞–ª–∞–Ω—Å–µ –ª–£–ª", "red");
    }

    callMethod("createExchangeOffer", {
      qiwiAccount,
      costOneDC,
      durkaCoins: inputDurkaCoins,
    })
      .then(({ status, message }) => {
        console.log({ status, message });

        if (status === "denied") {
          return openAlertPay(message, "red");
        }

        // openAlertPay(message, "green"); // —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± —É—Å–ø–µ—à–Ω–æ–º –¥–æ–±–∞–≤–ª–µ–Ω–∏–∏ –ø—Ä–µ–¥–ª–æ–∂–∑–µ–Ω–∏—è
        getExchangeList();

        setCostOneDC("");
        setInputDurkaCoins("");
        setQiwiAccount("+7");
        tabBuy();
        openSharingModal();
      })
      .catch((error) => {
        console.log("createExchangeOffer error", error);
      });
  }

  function onChangeAction(e, type, _costOneDC, remainingDC) {
    const value = e.target.value.trim();

    switch (type) {
      case "DC":
        setInputDurkaCoins(value);
        break;
      case "costOneDC":
        setCostOneDC(value.replace(",", "."));
        break;
      case "qiwiAccount":
        setQiwiAccount(value);
        break;
      case "modal":
        let text = `–ö –æ–ø–ª–∞—Ç–µ: ${(value * _costOneDC).toFixed(2)} —Ä—É–±`;
        if (Number.isNaN(+value)) {
          text = `–°—É–º–º–∞ –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å —á–∏—Å–ª–æ–º`;
        }

        if ((+value ^ 0) !== +value) {
          text = "DurkaCoins –¥–æ–ª–∂–Ω—ã –±—ã—Ç—å —Ü–µ–ª—ã–º —á–∏—Å–ª–æ–º";
        }

        if (remainingDC / 100_000 < value) {
          text = `–ö–æ–ª-–≤–æ –Ω–µ –¥–æ–ª–∂–Ω–æ –ø—Ä–µ–≤—ã—à–∞—Ç—å –æ—Å—Ç–∞–≤—à–∏–µ—Å—è –Ω–∞ –ø—Ä–æ–¥–∞–∂—É DurkaCoins`;
        }

        document.querySelector("#exchangeBuy").innerHTML = text;
        amountDurkaCoins = Number(value);
        break;
    }
  }

  function buyExchangeOffer(userId) {
    callMethod("buyExchangeOffer", {
      durkaCoins: amountDurkaCoins,
      offerUserId: userId,
    }).then(({ status, message }) => {
      getExchangeList();
      setActiveModal(null);
      return openAlertPay(message, status === "denied" ? "red" : "green");
    });
  }

  function openExchangeBuy(e) {
    targetIdExchangeBuy = e.currentTarget.id;
    const { costOneDC, remainingDC, userId } = dataExchangeBuy[
      targetIdExchangeBuy - 1
    ];

    setActiveModal(
      <ModalExchangeBuy
        userId={userId}
        targetIdExchangeBuy={targetIdExchangeBuy}
        setActiveModal={setActiveModal}
        costOneDC={costOneDC}
        remainingDC={remainingDC}
        onChangeAction={onChangeAction}
        buyExchangeOffer={buyExchangeOffer}
      />
    );
  }

  // let sliceItem = 50;
  const renderExchangeBuy = dataExchangeBuy
    .slice(0, sliceItem)
    .map((item, index) => {
      const dc = item.durkaCoins / 100_000;
      const remDC = item.remainingDC / 100_000;
      const urlUser = "https://vk.com/id" + item.userId;
      const sumRUB = ((item.durkaCoins / 100_000) * item.costOneDC).toFixed(2);
      const remainingAmountRUB = (
        (item.remainingDC / 100_000) *
        item.costOneDC
      ).toFixed(2);

      return (
        <ExchangeBuyItem
          key={index}
          id={index + 1}
          userId={item.userId}
          fullName={item.fullName}
          photoURL={item.photoURL}
          costOneDC={item.costOneDC}
          openExchangeBuy={openExchangeBuy}
          urlUser={urlUser}
          sumRUB={sumRUB}
          remainingAmountRUB={remainingAmountRUB}
          dc={dc}
          remDC={remDC}
          fetchedUser={fetchedUser}
          deleteExchangeOffer={deleteExchangeOffer}
        />
      );
    });

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              flexDirection: "row",
            }}
          >
            <PanelHeaderBack onClick={goHome} data-to="home" />
            <Div
              style={{
                fontSize: "10px",
                color: "green",
                display: "flex",
                alignItems: "start",
                justifyContent: "center",
                flexDirection: "column",
              }}
            >
              <Div>–°–æ–æ–±—â–∏—Ç—å –æ–±</Div>
              <Div>–æ—à–∏–±–∫–µ:</Div>
              <Div>
                <Link href="https://vk.me/fakeman.cat_fmc" target="_blank">
                  –ü–æ–¥–¥–µ—Ä–∂–∫–∞
                </Link>
              </Div>
            </Div>
          </Div>
        }
      >
        –ë–∏—Ä–∂–∞ (beta)
      </PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "–í—ã –ø–æ–¥–∫–ª—é—á–∏–ª–∏—Å—å —Å 2 —É—Å—Ç—Ä–æ–π—Å—Ç–≤ ",
          socketReconnect,
          "–ü–µ—Ä–µ–ø–æ–¥–∫–ª—é—á–∏—Ç—å—Å—è"
        )
      ) : (
        <>
          <FixedLayout vertical="top" filled={true}>
            <Tabs>
              <TabsItem onClick={tabBuy} selected={activeTab === "buy"}>
                –ö—É–ø–∏—Ç—å
              </TabsItem>
              <TabsItem onClick={tabSell} selected={activeTab === "sell"}>
                –ü—Ä–æ–¥–∞—Ç—å
              </TabsItem>
            </Tabs>
          </FixedLayout>

          {networkStatus ? (
            stub(
              "–û—Ç—Å—É—Ç—Å—Ç–≤—É–µ—Ç –∏–Ω—Ç–µ—Ä–Ω–µ—Ç - —Å–æ–µ–¥–∏–Ω–µ–Ω–∏–µ!",
              updateConnection,
              "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å"
            )
          ) : (
            <>
              {socketReconnection ? (
                stub(
                  "–û—à–∏–±–∫–∞ –ø–æ–¥–∫–ª—é—á–µ–Ω–∏—è –∫ —Å–µ—Ä–≤–µ—Ä—É",
                  socketReconnect,
                  "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å"
                )
              ) : (
                <>
                  {activeTab === "buy" ? (
                    <>
                      {/* {renderInventory.length
                                                ? */}

                      <Div style={{ marginTop: 55 }}>
                        <Group>
                          <Div
                            style={{
                              display: "flex",
                              flexDirection: "column",
                              marginLeft: 20,
                              marginRight: 20,
                            }}
                          >
                            <Div
                              style={{
                                display: "flex",
                                alignItems: "center",
                                flexDirection: "row",
                                justifyContent: "space-between",
                                // marginLeft: 20,
                                // marginRight: 20,
                                marginBottom: 10,
                              }}
                            >
                              <Div
                                style={{
                                  display: "flex",
                                  alignItems: "center",
                                  flexDirection: "row",
                                }}
                              >
                                <Div>–ë–∞–ª–∞–Ω—Å: {formatCoins(durkaCoins)}</Div>
                                <Div
                                  className="iconDC"
                                  style={{ marginLeft: 8 }}
                                />
                              </Div>

                              <Div>
                                <Button
                                  style={{
                                    backgroundColor: "green",
                                    color: "white",
                                  }}
                                  size="m"
                                  onClick={openBuyDurkaCoins}
                                >
                                  –ü–æ–ø–æ–ª–Ω–∏—Ç—å
                                </Button>
                              </Div>
                            </Div>
                            <Div
                              style={{
                                display: "flex",
                                alignItems: "center",
                                flexDirection: "row",
                                justifyContent: "space-between",
                                // marginLeft: 20,
                                // marginRight: 20
                              }}
                            >
                              <Div>–ë–∞–ª–∞–Ω—Å: {rubles} ‚ÇΩ</Div>
                              <Div
                                style={{
                                  display: "flex",
                                  alignItems: "center",
                                  flexDirection: "row",
                                }}
                              >
                                <Button
                                  style={{
                                    backgroundColor: "#D40000",
                                    color: "white",
                                  }}
                                  size="m"
                                  onClick={openWithdraw}
                                  // style={{ marginLeft: 8 }}
                                >
                                  –í—ã–≤–µ—Å—Ç–∏
                                </Button>
                                <Button
                                  size="m"
                                  onClick={openReplenishment}
                                  style={{
                                    marginLeft: 8,
                                    backgroundColor: "green",
                                    color: "white",
                                  }}
                                >
                                  –ü–æ–ø–æ–ª–Ω–∏—Ç—å
                                </Button>
                              </Div>
                            </Div>
                          </Div>
                        </Group>

                        <Group>
                          <Div
                            style={{
                              paddingLeft: 5,
                              paddingRight: 5,
                            }}
                          >
                            <Div
                              style={{
                                paddingLeft: 5,
                                paddingRight: 5,
                              }}
                            >
                              <Button
                                size="l"
                                stretched
                                onClick={getExchangeList}
                                style={{
                                  marginTop: 10,
                                  marginBottom: 10,
                                  backgroundColor: "green",
                                  color: "white",
                                }}
                              >
                                –û–±–Ω–æ–≤–∏—Ç—å —Å–ø–∏—Å–æ–∫
                              </Button>
                            </Div>
                            <>
                              {!renderExchangeBuy.length ? (
                                <div className="spinner">
                                  <Spinner
                                    size="large"
                                    style={{ marginTop: 90 }}
                                  />
                                </div>
                              ) : (
                                <>
                                  <Div> {renderExchangeBuy} </Div>
                                  <Div
                                    style={{
                                      marginTop: "15px",
                                      marginBottom: "15px",
                                    }}
                                  >
                                    <Button
                                      size="l"
                                      stretched
                                      onClick={() =>
                                        setSliceItem(sliceItem + 50)
                                      }
                                      style={{
                                        // marginTop: 10,
                                        // marginBottom: 10,
                                        backgroundColor: "green",
                                        color: "white",
                                      }}
                                    >
                                      –ü–æ–∫–∞–∑–∞—Ç—å –µ—â—ë
                                    </Button>
                                  </Div>
                                </>
                              )}
                            </>
                          </Div>
                        </Group>
                      </Div>
                      {/* // : */}
                      {/* <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "column",
                        }}
                      >
                        <Spinner size="large" style={{ marginTop: 90 }} />
                      </div> */}
                      {/* // } */}
                    </>
                  ) : (
                    ""
                  )}

                  {activeTab === "sell" ? (
                    <>
                      <Div
                        style={{
                          marginTop: 55,
                        }}
                      >
                        <ExchangeSell
                          durkaCoins={formatCoins(durkaCoins)}
                          course={course}
                          addExchangeOffer={addExchangeOffer}
                          onChangeAction={onChangeAction}
                          costOneDC={costOneDC}
                          inputDurkaCoins={inputDurkaCoins}
                          qiwiAccount={qiwiAccount}
                          openPaymentVIP={openPaymentVIP}
                          openInfoVIP={openInfoVIP}
                        />
                      </Div>
                    </>
                  ) : (
                    ""
                  )}
                </>
              )}

              {snackbar}
            </>
          )}
        </>
      )}
    </Panel>
  );
};

export { Exchange };
