import React from "react";
import { Cell, Div, Group, Header } from "@vkontakte/vkui";

import styles from "../styles/custom-styles.sass";

/**
 * @param {Object} params - –ü–∞—Ä–∞–º–µ—Ç—Ä—ã
 * @param {number} params.id - –ê–π–¥–∏ –ø—Ä–µ–¥–º–µ—Ç–∞
 * @param {string} params.name - –ò–º—è –ø—Ä–µ–¥–º–µ—Ç–∞
 * @param {number} params.ratingMin - –ú–∏–Ω–∏–º–∞–ª—å–Ω—ã–π —Ä–µ–π—Ç–∏–Ω–≥
 * @param {number} params.ratingMax - –ú–∞–∫—Å–∏–º–∞–ª—å–Ω—ã–π —Ä–µ–π—Ç–∏–Ω–≥
 * @param {number} params.pillBlue - –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–∏–Ω–∏—Ö –ø–∏–ª—é–ª—å –≤ –Ω–µ–¥–µ–ª—é
 * @param {number} params.pillRed - –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –∫—Ä–∞—Å–Ω—ã—Ö –ø–∏–ª—é–ª—å –≤ –Ω–µ–¥–µ–ª—é
 * @param {number} params.pillGold - –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –∑–æ–ª–æ—Ç—ã—Ö –ø–∏–ª—é–ª—å –≤ –Ω–µ–¥–µ–ª—é
 * @param {number} params.shirt - –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–º–∏—Ä–∏—Ç–µ–ª—å–Ω—ã—Ö —Ä—É–±–∞—à–µ–∫ –≤ –Ω–µ–¥–µ–ª—é
 * @param {number} params.cell - –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –æ–¥–∏–Ω–æ—á–Ω—ã—Ö –∫–∞–º–µ—Ä –≤ –Ω–µ–¥–µ–ª—é
 * @param {number} params.vacation - –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –æ—Ç–ø—É—Å–∫–æ–≤ –≤ –Ω–µ–¥–µ–ª—é
 */
const RanksItem = ({
  id,
  name,
  ratingMin,
  ratingMax,
  pillBlue,
  pillRed,
  pillGold,
  shirt,
  cell,
  vacation,
}) => {
  return (
    <Group header={<Header mode="secondary">{name}</Header>}>
      <Cell
        style={{ margin: 0, padding: 0 }}
        asideContent={`${ratingMin} - ${ratingMax || Infinity}`}
      >
        {name.toLowerCase() === "—à–∏–∑–æ–∏–¥" ? (
          <Div>–ù–∏—á–µ–≥–æ –Ω–µ –ø–æ–ª—É—á–∞–µ—Ç.</Div>
        ) : (
          <>
            <Div>–ü–æ–ª—É—á–∞–µ—Ç –≤ –Ω–µ–¥–µ–ª—é:</Div>
            {pillBlue ? <Div>Blue –ø–∏–ª—é–ª—å: {pillBlue} —à—Ç</Div> : ""}
            {pillRed ? <Div>Red –ø–∏–ª—é–ª—å: {pillRed} —à—Ç</Div> : ""}
            {pillGold ? <Div>Gold –ø–∏–ª—é–ª—å: {pillGold} —à—Ç</Div> : ""}
            {vacation ? <Div>–û—Ç–ø—É—Å–∫–æ–≤: {vacation} —à—Ç</Div> : ""}
            {shirt ? <Div>–°–º–∏—Ä–∏—Ç–µ–ª—å–Ω—ã—Ö —Ä—É–±–∞—à–µ–∫: {shirt} —à—Ç</Div> : ""}
            {cell ? <Div>–û–¥–∏–Ω–æ—á–Ω—ã—Ö –∫–∞–º–µ—Ä: {cell} —à—Ç</Div> : ""}
          </>
        )}
      </Cell>
    </Group>
  );
};

export { RanksItem };
